.form

  width: 50%
  display: flex
  flex-direction: column
  justify-content: center
  align-items: center
  color: inherit
  font-size: 16px
  padding: 1em

  @media screen and (max-width: 768px)
    width: 100%
    max-width: 550px
    font-size: 14px

  &__main
    max-width: 450px
  &__title

    font-weight: 500
    font-size: 2em
    margin: 0 0 .5em
    @media screen and (max-width: 768px)
      text-align: center

  &__subtitle
    font-weight: 400
    color: inherit
    font-size: 1em
    margin: 0
    @media screen and (max-width: 768px)
      text-align: center

    &--next-line
      margin-top: .15em
      display: block

  &__link
    font-weight: 400
    font-size: 16px
    font-family: inherit

  &__container
    width: 100%
    margin: 3em 0
    display: flex
    flex-direction: column
    gap: 3em

  &__block
    position: relative
    width: 100%

    &--img
      position: absolute
      width: 20px
      top: 50%
      transform: translateY(-50%)
      z-index: 2

  &__input
    position: relative
    z-index: 1
    padding-left: 27px !important

  &__other
    width: 100%

  &__description
    text-align: center
    color: #B5B5B5
    font-size: 1em
    font-weight: 500
    margin: 2em 0

  &__icons
    width: 100%
    display: flex
    justify-content: center
    gap: 1.5em

  &__icon
    width: 42px
    cursor: pointer