@import "./../../colors"

.blockBar
  position: relative
  top: 0
  right: 0
  z-index: 10

  &__arrow
    //position: absolute
    position: relative
    display: flex
    flex-direction: column
    gap: 3px
    padding: 5px

    cursor: pointer
    z-index: 10

    & span
      display: block
      width: 8px
      height: 8px

      -webkit-border-radius: 100%
      -moz-border-radius: 100%
      border-radius: 100%

      background: #333333

  & .toolbar__btn
    text-transform: revert

    width: 40px

  & .toolbar__list
    position: sticky
    top: .5rem
    display: block
    flex-direction: column
    z-index: 11

    &:before, &:after
      content: none
      