.App {
  width: 100%;
  min-height: 100vh;
  padding: 1px 10px;
  display: flex;
  flex-direction: column;
}
.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}
.icon {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform .2s

}

.icon:hover {
    transform: scale(1.1)
}