.login
  display: flex
  width: 100%
  padding: 20px 0
  flex-grow: 1
  position: relative
  @media screen and (max-width: 768px)
    flex-direction: column-reverse
    align-items: center
    justify-content: center

.logo
  position: absolute
  width: 56px
  top: 10px
  left: 10px
  @media screen and (max-width: 768px)
    top: 5px
    left: 0

  &--img
    width: 100%
    max-width: 100%


.preview
  width: 50%
  background: #000842
  border-radius: 15px
  display: flex
  flex-direction: column
  align-items: center
  justify-content: center
  font-size: 20px
  padding: 1em

  color: #fff

  @media screen and (max-width: 768px)
    width: 100%
    max-width: 450px
    margin-top: 30px
    font-size: 16px

  &__container
    max-width: 486px

  &--img
    width: 100%
    max-width: 100%

    @media screen and (max-width: 768px)
      max-width: 300px

  &__img
    width: 100%
    display: flex
    justify-content: center
    margin-bottom: 6vh

    @media screen and (max-width: 768px)
      margin-bottom: 1em
      display: none

  &__title
    font-weight: 600
    font-size: 2em
    color: inherit
    margin: 0 0 .5em
    @media screen and (max-width: 768px)
      text-align: center


  &__subtitle
    color: inherit
    font-size: 1em
    margin: 0
    @media screen and (max-width: 768px)
      text-align: center