@import './../../colors'

.toolbar
  position: absolute
  &--mini

  &__list
    position: relative
    display: flex
    background: $background
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25)
    border-radius: 2px
    border: 1px solid $border
    &:before, &:after
      top: 100%
      left: 50%
      content: ""
      border: solid transparent
      height: 0
      width: 0
      position: absolute

    &:before
      border-color: $border hsla(0,0%,87%,0) hsla(0,0%,87%,0)
      border-width: 6px
      margin-left: -6px

    &:after
      border-color: $background hsla(0,0%,100%,0) hsla(0,0%,100%,0)
      border-width: 4px
      margin-left: -4px



.toolbar__btn
  display: flex
  align-items: center
  justify-content: center
  color: $color-text
  outline: none
  border: none
  background: none
  cursor: pointer
  font-size: 18px
  text-transform: uppercase
  height: 34px
  width: 30px
  border-radius: 4px
  background: $background
  &:hover
    background: $hover-btn