.button 
	background: #0C21C1
	box-shadow: 0px 4px 26px rgba(0, 0, 0, 0.25)
	border-radius: 32px
	font-size: 1.2em
	padding: .65em
	color: #FFFFFF
	outline: none
	border: 1px solid #0C21C1
	cursor: pointer
	transition: transform .5s

	&:hover
		transform: scale(0.95)

		@media screen and (max-width: 768px)
			transform: none