@function theme-colors($theme)
  $background: null
  $border: null
  $color-text: null
  $hover-btn: null
  $border-color-box: null

  @if $theme == 'light'
    $background: #FBFBFB
    $border: #DDDDDD
    $color-text: #888888
    $hover-btn: #F3F3F3
    $border-color-box: #DDDDDD
  @else if $theme == 'dark'
    $background: #333333
    $border: #111111
    $color-text: #FFFFFF
    $hover-btn: #444444
    $border-color-box: #DDDDDD


  @return ($background, $border, $color-text, $hover-btn, $border-color-box)

$colors: theme-colors('dark')
$background: nth($colors, 1)
$border: nth($colors, 2)
$color-text: nth($colors, 3)
$hover-btn: nth($colors, 4)
$border-color-box: nth($colors, 5)

@mixin box-style 
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25)
  border-radius: 2px
  border: 1px solid $border-color-box
